	module.exports = {"data":{"site":{"siteMetadata":{"title":"GREG JOHNS","author":"Greg Johns"}},"markdownRemark":{"id":"/Users/gregjohns/Projects/Greg-Johns/src/pages/2015-11-09-ants/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/escher_ant-b76db06f4dac6e2b76c65b3de23856bd-9b0e7.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 48.33333333333333%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"ants\"\n        title=\"\"\n        src=\"/static/escher_ant-b76db06f4dac6e2b76c65b3de23856bd-f8fb9.jpg\"\n        srcset=\"/static/escher_ant-b76db06f4dac6e2b76c65b3de23856bd-e8976.jpg 148w,\n/static/escher_ant-b76db06f4dac6e2b76c65b3de23856bd-63df2.jpg 295w,\n/static/escher_ant-b76db06f4dac6e2b76c65b3de23856bd-f8fb9.jpg 590w,\n/static/escher_ant-b76db06f4dac6e2b76c65b3de23856bd-9b0e7.jpg 600w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>Or Human stigmergy at work</h4>\n<blockquote>\n<p>Note: <del>Metalsmith</del> now Gatsby. I recently changed static site generator to <a href=\"https//www.gatsbyjs.org\">Gatsby</a>. If your into React and you should check it out.</p>\n</blockquote>\n<p>This is my first post with a shiny new <a href=\"http://www.metalsmith.io/\">metalsmith</a> engine blog thing and without getting too lengthy, I just wanted to get something down for posterity and a bit of refocus on <strong>why</strong> write. The problem with thinking about “why write” can get reflective, which leads procrastination, writer’s block and bad places for a writer. Sure enough I soon found myself mindlessly surfing the web.</p>\n<p>Or so I thought. Somewhere in the winding link’s I found my way to  reading a <a href=\"http://bitworking.org/news/Stigmergy\">blog article</a> from <a href=\"http://bitworking.org/news/bio\">Joe Gregorio</a> who talks about how the <em>Web = Stigmergy</em>. If your not sure what Stigmergy is, I’ll pull the same quote Joe used to describe a term French biologist Pierre-Paul Grasse used to describe interaction through the environment.</p>\n<blockquote>\n<p>Self-Organization in social insects often requires interactions among insects: such interactions can be direct or indirect. Direct interactions are the “obvious” interactions: antennation, trophallaxis (food or liquid exchange), mandibular contact, visual contact, chemical contact (the odor of nearby nestmates), etc. Indirect interactions are more subtle: two individuals interact indirectly when one of then modifies the environment and the other responds to the new environment at a later time. Such an interaction is an example of stigmergy.</p>\n</blockquote>\n<p>In an example he talks about how an ant use a pheremone trails to communicate food source to other ants. He goes on to claim that <em>The World-Wide Web is the first stimeric communication medium for humans</em>.</p>\n<p>It’s was all interesting and of course when I start reading quotes from old scientist then I know I’m getting somewhere. But wait… I was suppose to write something. Ok, back to my own article. Why am I writing again… Oh yeah, because I’ve always enjoyed it and as for as posting writing on the web I guess I’m just trying to help out any other interested ants.</p>\n<blockquote>\n<p>Thanks to <a href=\"http://blakeembrey.com/\">Blake Embrey</a> for putting up his help-full build pipeline that I forked for this site and I would like to add to Joe’s claim that <strong>open source</strong> could be a binary equivalent to pheromones.</p>\n</blockquote>","frontmatter":{"title":"Ants","date":"November 09, 2015"}}},"pathContext":{"path":"/ants/"}}

